import { useState } from 'react';

const ChildA1 = () => {
	console.log('render', 'ChildA1');
	return (<div className="note-demo-child-box">ChildA1 区域</div>)
}
const ChildA2 = () => {
	console.log('render', 'ChildA2');
	return (<div className="note-demo-child-box">ChildA2 区域</div>)
}
const ChildA3 = () => {
	console.log('render', 'ChildA3');
	return (<div className="note-demo-child-box">ChildA3 区域</div>)
}

const Demo = () => {
	const [countA, setCountA] = useState(11);
	
	const fn = () => {
		console.log('点击事件', '父组件 Demo01 变量 countA 被重新赋值', '所有子组件全部重新渲染');
		setCountA(countA + 1);
	}
	console.log('render', 'Demo01', '--------------------');
	return (
		<div className="note-example-box">
			<div className="note-example-intro">该[主组件]重新渲染时,[子组件][会]全部重新渲染。</div>
			<div className="note-demo-parent-box">
				<button onClick={fn}>countA:{countA}</button>
				<ChildA1></ChildA1>
				<ChildA2></ChildA2>
				<ChildA3></ChildA3>
			</div>
			<div className="note-code-case">
				<textarea defaultValue={codeVal1} style={{height: '480px'}}></textarea>
				<textarea defaultValue={codeVal2} style={{height: '300px'}}></textarea>
			</div>
		</div>
	);
};

const codeVal1 = `父组件 Demo01.jsx
const Demo = () => {
	const [countA, setCountA] = useState(11);
	
	const fn = () => {
		console.log('点击事件', '父组件 Demo01 变量 countA 被重新赋值', '所有子组件全部重新渲染');
		setCountA(countA + 1);
	}
	console.log('render', 'Demo01', '--------------------');
	return (
		<div className="note-example-box">
			<div className="note-demo-parent-box">
				<button onClick={fn}>countA:{countA}</button>
				<ChildA1></ChildA1>
				<ChildA2></ChildA2>
				<ChildA3></ChildA3>
			</div>
		</div>
	);
};
`;
